/* EDITOR */

:root {
    --editor-font-family: var(--font-family);
}

#content {
    position: absolute;
    right: 0;
    width: 50%;
    min-width: 160px;
    vertical-align: top;
}

.CodeMirror {
    font-size: 110%;
    line-height: 1.4;
}

.CodeMirror-linenumbers {
    padding: 0 8px;
}

.cm-s-tangram.CodeMirror { color: #999; background-color: #fff; }
.cm-s-tangram .CodeMirror-selected { background: #cfc !important; }
/*.cm-s-tangram.CodeMirror { background-image: url(data:image/png;base64,); }*/
.cm-s-tangram .CodeMirror-gutters { background: #f8f8f8; color: #303030; }
.cm-s-tangram .CodeMirror-linenumber { color: #aaa; margin-left: 14px; }
div.cm-s-tangram .CodeMirror-cursor { border-left: 2px solid #222; }

.cm-s-tangram .cm-keyword { color: #07a; }
.cm-s-tangram .cm-builtin { color: #07a; }
.cm-s-tangram .cm-atom { color: #D16768; }
.cm-s-tangram .cm-number { color:  #ca7841; }
.cm-s-tangram .cm-def { color: #8DA6CE; }
.cm-s-tangram .cm-variable { color: #4D5C64; }
.cm-s-tangram .cm-property { color: #905; }
.cm-s-tangram .cm-qualifier { color: #690; }
.cm-s-tangram .cm-operator { color: #cda869; }
.cm-s-tangram .cm-comment { color:#777; font-weight:normal; }
.cm-s-tangram .cm-meta { color: #000; }
.cm-s-tangram .cm-string { color:#07a; font-style:italic; }
.cm-s-tangram .cm-string-2 { color:#bd6b18; }
.cm-s-tangram .cm-tab {
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAMCAYAAAAkuj5RAAAAAXNSR0IArs4c6QAAAGFJREFUSMft1LsRQFAQheHPowAKoACx3IgEKtaEHujDjORSgWTH/ZOdnZOcM/sgk/kFFWY0qV8foQwS4MKBCS3qR6ixBJvElOobYAtivseIE120FaowJPN75GMu8j/LfMwNjh4HUpwg4LUAAAAASUVORK5CYII=);
    background-position: right;
    background-repeat: no-repeat;
}
.cm-s-tangram .cm-tag { color: #997643; }
.cm-s-tangram .cm-attribute { color: #d6bb6d; }
.cm-s-tangram .cm-header { color: #FF6400; }
.cm-s-tangram .cm-hr { color: #AEAEAE; }
.cm-s-tangram .cm-link { color:#ad9361; font-style:italic; text-decoration:none; }
.cm-s-tangram span.cm-variable-2, .cm-s-tangram span.cm-tag { color: #690; }
.cm-s-tangram span.cm-variable-3, .cm-s-tangram span.cm-def { color: #07a; }
.cm-s-tangram .cm-error { border-bottom: 1px solid red; }

div.cm-s-tangram .CodeMirror-activeline-background { background: #efefff; }
div.cm-s-tangram span.CodeMirror-matchingbracket {
    outline:1px solid #A8ABAA;
    color: #FCD812;
}

.CodeMirror-foldmarker { font-family: arial; }
.CodeMirror-foldgutter { width: .9em; }
.CodeMirror-foldgutter-open,
.CodeMirror-foldgutter-folded { color: #555; /*cursor: pointer;*/ }
.CodeMirror-foldgutter-open:after { content: "\25BE"; }
.CodeMirror-foldgutter-folded:after { content: "\25B8"; }

/* SUGGESTED KEYS */
.cm-suggested-keys-menu-remove {
    float: right;
    cursor: pointer;
}

.cm-suggested-keys-menu-btn {
    font-family: var(--editor-font-family);
    font-size: 14px;
    color: black;
    cursor: pointer;
}

.cm-suggested-keys-menu {
    font-family: var(--editor-font-family);
    font-size: 14px;
    /*background: #494949;*/
    background: #fff;
    color: white;
    height: 20px;
    padding: 3px 7px;
}
